.bannerImg {
  width: 100%;
  border-radius: 10px;
}

.bannerImg:hover {
  border: 1px solid paleturquoise;
}
.content {
  padding: 20px;
}
.swiper-slide {
  padding: 20px 0;
  position: static !important;
  transform: none !important;
  transform: all 0.3s !important;
}
.swiper-slide.swiper-slide-next {
  transform: scale(1.2) !important;
}
.content_main {
  width: 95%;
  display: flex;
  justify-content: space-around;
  position: relative;
  left: 50%;
  transform: translateX(-50%);
}
.content_main_text {
  font-size: 16px;
  color: aqua;
}
.content_main_text:hover {
  font-weight: bold;
  transform: scale(1.2);
  transition: all .3s;
  cursor: pointer;
}
