.menuBox {
  position: fixed;
  top: 50%;
  transform: translateY(-50%);
  left: 10px;
  border-radius: 30px;
  background: linear-gradient(to bottom, rgb(55 86 117), rgb(176 165 140));
  z-index: 1000;
}

.menuItem {
  height: 45px;
  width: 60px;
  font-size: 29px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.menuItem:hover {
  transform: scale(1.2);
  transition: all 0.2s;
  cursor: pointer;
}
